footer {
  margin: 0 auto;
  padding-bottom: 25px;

  background-color: $pink-background-color;

  @media screen and (min-width: $tablet) {
    padding-bottom: 18px;
  }

  @media screen and (min-width: $desktop) {
    padding-bottom: 10px;
  }

  .container {
    padding-top: 0px;
    padding-bottom: 43px;

    @media screen and (min-width: $tablet) {
      padding-bottom: 91px;
    }

    @media screen and (min-width: $desktop) {
      padding-bottom: 97px;
    }
  }
}

.footer-basement-line {
  padding-top: 22px;

  border: 1px solid $pink-background-color;
  border-top-color: $gray-color;

  @media screen and (min-width: $tablet) {
    padding-top: 29px;
  }

  @media screen and (min-width: $desktop) {
    padding-top: 9px;
  }
}

.footer-slogan-socaddress {
  @media screen and (min-width: $tablet) {
    display: flex;
    justify-content: space-between;
  }
}

.footer-slogan {
  margin-bottom: 43px;

  @media screen and (min-width: $tablet) {
    margin: 0px;
  }
}

.footer-slogan-top {
  width: 236px;
  margin-bottom: 17px;
  font-family: $title-font;
  font-size: 18px;
  line-height: 1.17;
  text-transform: uppercase;

  color: $pink-color;

  @media screen and (min-width: $tablet) {
    width: 346px;
    margin-bottom: 16px;
    font-size: 26px;
    line-height: 1.15;
  }

  @media screen and (min-width: $desktop) {
    width: 500px;
    margin-bottom: 15px;
    font-size: 30px;
    line-height: 1.13;
  }
}

.footer-slogan-bottom {
  width: 200px;
  font-family: $title-font;
  font-size: 34px;
  line-height: 1.15;
  letter-spacing: 0.06em;
  text-transform: uppercase;

  color: $main-title-color;

  @media screen and (min-width: $tablet) {
    font-size: 48px;
    line-height: 1.15;
  }

  @media screen and (min-width: $desktop) {
    width: 500px;
    font-size: 58px;
    line-height: 1.14;
  }
}

.footer-socaddress {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.footer-soc-list {
  display: flex;
  margin-bottom: 46px;

  @media screen and (min-width: $desktop) {
    margin-bottom: 50px;
  }
}

.footer-soc-list-item:not(:last-child) {
  margin-right: 28px;

  @media screen and (min-width: $tablet) {
    margin-right: 30px;
  }

  @media screen and (min-width: $desktop) {
    margin-right: 25px;
  }
}

.footer-soc-list-item-link {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-soc-list-item-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;

  background-color: $pink-color;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: $main-title-color;
  }

  @media screen and (min-width: $tablet) {
    width: 34px;
    height: 34px;
  }
}

.footer-address {
  display: inline-flex;
}

.footer-address-link {
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.83;

  text-align: right;
  letter-spacing: 0.02em;

  color: $main-title-color;

  @media screen and (min-width: $tablet) {
    font-size: 14px;
    line-height: 1.86;
  }

  @media screen and (min-width: $desktop) {
    font-size: 16px;
    line-height: 1.86;
  }
}

.footer-address-link-description {
  display: inline-flex;
  align-items: center;
  font-weight: 700;
  letter-spacing: 0.02em;

  color: $black-color;
}

.footer-basement {
  width: 150px;
  height: 42px;
  margin: 0 auto;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  color: $gray-color;

  @media screen and (min-width: $tablet) {
    width: 276px;
    height: 26px;
    letter-spacing: 0.02em;
  }
}
